<template>
	<view class="content">
		<view class="nav">
			<view class="banner">
				<image :src="projectpic" mode="aspectFill"></image>
				<view class="navtitle">
					<view class="yello"></view>
					<text class="navtext">{{data.title}}</text>
					<text class="price">{{data.price}}/次</text>
				</view>
			</view>
		</view>
		<uni-list>
			<navigator url="/pages/project/index" open-type="switchTab">
			<uni-list-item title="选择其他项目">
					<p class="list-text">{{data.title}}</p>
			</uni-list-item>
			</navigator>
			<navigator :url="'/pages/project/staffList?pr_id='+data.pr_id">
			<uni-list-item :title=stname>
				<p class="list-text">{{subscribetime}}</p>
			</uni-list-item>
			</navigator>
			<uni-list-item title="服务时长">
				<p class="list-text">{{data.duration}}分钟</p>
			</uni-list-item>
			<navigator  url="../myself/address">
			<uni-list-item title="当前地址">
				<p class="list-text">{{address}}-{{name}}</p>
			</uni-list-item>	
			</navigator>
		</uni-list>
		<button class="submit" @click="subscribe()" size="mini">确认预约</button>
		<view class="detalis">
			<view class="banner">
				<view class="navtitle">
					<view class="yello">
					</view>
					<text class="navtext">项目简介</text>
				</view>
				<uni-list>
					<view class="detalisdesc">
						{{data.desc}}
				</view>
					<view class="dinggouxuzhi">
						<h6>订购须知</h6>
						<view class="detalisdesc">
							1.本店严格拒绝一切形式的非正规服务，<br>
							2.订单确认后，修改订单或退款需提前2小时通知；<br>
							3.预约时间前2小时内退单将按30元/人扣取误工费；<br>
							4.如商家接单后爽约将全额退款并向您额外赔付30元；<br>
							5.为保障您的权益，所有费用请通过董杨平台支付；<br>
							6.本店24小时上门服务，但因夜间交通不便，晚21:00～早07:30间服务的订单，需另行支付技师往返打车费用（往返金额共计不超过100元）。<br>
							</br>
						</view>
					</view>
				</uni-list>
			</view>
		</view>

	</view>
</template>

<script>
	import uniList from "../../components/uni-list/uni-list.vue"
	import uniListItem from "../../components/uni-list-item/uni-list-item.vue"
	export default {
		components: {
			uniList,
			uniListItem
		},
		data() {
			return {
				data:'',
				pic_1:'',
				st_id:0,
				subscribetime:'请选择时间',
				selectUser:false,
				stname:'请选择技师',
				address:'请选择地址',
				projectpic:'',
				name:''
			}
			},
		onLoad(options) {
			this.checkLogin()
			if(options.stname!=undefined){
				this.stname = options.stname;
			}
			if(options.subscribetime!=undefined){
				this.subscribetime = options.subscribetime;
			}
			this.st_id = options.st_id;
			this.pic_1 = options.pic_1;
			this.pr_id = options.pr_id;
			let pr_id = options.pr_id;
			uni.request({
				url:this.apiServer+"/api/project/"+pr_id,
				method:'GET',
				success: (res) => {
					this.data = res.data;
					this.projectpic =  this.apiServer+'/uploads/'+res.data.photo
				}
			});
			var t = this;
			setInterval(function(){
				uni.request({
					url: t.apiServer+'/api/address/index?user_id='+uni.getStorageSync('USERID'),
					method: 'GET',
					success: res => {
						var data = res.data;
						if(data.err==1){
 /* 						uni.showModal({
								title:'错误提示',
								content:'请完善您的服务地址',
								showCancel:false
							}); */
						}else{
							t.address = data.data.address;
							t.name = data.data.name;
							t.mobile = data.data.mobile;
							t.longitude = data.data.longitude;
							t.latitude = data.data.latitude;
						}
					},
				});
			},2500)
			
		},
		onShow() {
			this.myaddress();
		},
		methods: {
			subscribe:function(){
				var tt = this;
				this.orderInfo = {
					st_id:this.st_id,
					pr_id:this.pr_id,
					subtime:this.subscribetime,
					address: this.address,
					address_contacts:this.name,
					address_mobile:this.mobile,
					longitude:this.longitude,
					latitude:this.latitude,
					user_id:uni.getStorageSync('USERID')
				};
				if(this.orderInfo.subtime=='请选择时间'){
					uni.showModal({
						title:"错误提示",
						content:"请选择预约时间",
						showCancel:false
					})
				}else{
				uni.showActionSheet({
				    itemList: ['余额支付', '支付宝支付', '微信支付'],
				    success: function (res) {
						if(res.tapIndex==0){
							tt.balancePay()// 余额支付
						}else if(res.tapIndex==1){
							tt.aliPay() // 支付宝支付
						}else if(res.tapIndex==2){
							tt.wxPay(); //微信支付
						}
				    },
				    fail: function (res) {
				        console.log(res.errMsg);
				    }
				});
			}},
			async aliPay(){

				var orderInfo =  await this.getOrderInfo();
				if (orderInfo.statusCode!== 200) {
				    uni.showModal({
				        content: "获得订单信息失败",
				        showCancel: false
				    })
				    return;
				}
				uni.requestPayment({
				    provider: 'alipay',
				    orderInfo: orderInfo.data,
				    success: (e) => {
				        console.log("success", e);
				        uni.showToast({
				            title: "支付成功"
				        })
				    },
				    fail: (e) => {
				        console.log("fail", e);
				        uni.showModal({
				            content: "支付失败",//+ e.errMsg,
				            showCancel: false
				        })
				    },
					complete: () => {
				        console.log("payment结束")
				        this.loading = false;
				    }
				})
			
			},
			wxPay(orderInfo){
				uni.showToast({
				    title: '暂未开通',
				    duration: 2000
				});
			},
			balancePay(orderInfo){  //余额支付
				uni.request({
					url:this.apiServer+"/api/order/save",
					data:orderInfo,
					method:'POST',
					success: (res) => {
						uni.showModal({
							title:'信息提示',
							content:res.data.msg,
							showCancel:false
						});
						if(res.data.err==0){
							uni.redirectTo({
								url: '/pages/myself/orders?status=0'
							});
						}
					}
				})
			},
			getOrderInfo() {
				var OrderInfo = this.orderInfo;
			    let url = this.apiServer+'/api/order/aliPay';
			    return new Promise((res) => {
			        uni.request({
						method:'POST',
			            url: url,
						data:OrderInfo,
			            success: (result) => {
			                res(result);
			            },
			            fail: (e) => {
							console.log(e);
			                res(e);
			            }
			        })
			    })
			},
			selectPay:function(){
				var payInfo = {
					st_id:this.st_id,
					pr_id:this.pr_id,
					subtime:this.subscribetime,
					address: this.address,
					address_contacts:this.name,
					address_mobile:this.mobile,
					longitude:this.longitude,
					latitude:this.latitude,
					user_id:uni.getStorageSync('USERID')
				};
				uni.setStorageSync('payInfo',payInfo);
				var s = uni.getStorageSync('payInfo');
			},
			myaddress(){//获取当前用户地址
				uni.request({
					url: this.apiServer+'/api/address/index?user_id='+uni.getStorageSync('USERID'),
					method: 'GET',
					success: res => {
						var data = res.data;
						if(data.err==1){
							uni.showModal({
								title:'错误提示',
								content:'请完善您的服务地址',
								showCancel:false
							});
						}else{
							this.address = data.data.address;
							this.name = data.data.name;
							this.mobile = data.data.mobile;
							this.longitude = data.data.longitude;
							this.latitude = data.data.latitude;
						}
					},
				});
			},
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		width: 750rpx;
		background-color: #f1f1f1;
		
	}

	.banner image {
		width: 750rpx;
		height: 300rpx;
	}

	.dinggouxuzhi {
		margin-top: 20rpx;
		background-color: #fdfaf0;
		white-space: pre-line;
	}

	.dinggouxuzhi h6 {
		margin-top: 10rpx;
		margin-left: 30rpx;
		color: #FF3000;
	}

	.dinggouxuzhi view {
		color: #823105;
	}

	.detalisdesc {
		margin-left: 30rpx;
		font-size: 24rpx;
		white-space: pre-line;
	}

	.list-text {
		text-align: right;
		font-size: 25rpx;
		margin-bottom: -38rpx;
		margin-top: 0rpx;
		line-height: 55rpx;

	}

	.uni-list {
		margin-top: 15rpx;
		background-color: #FFFFFF;
		margin-bottom: 30rpx;
	}

	.nav {
		background-color: #FFFFFF;
		height: 400rpx;
	}

	.navtext {
		font-size: 30rpx;
		font-weight: bold;
	}

	.price {
		margin-right: 38rpx;
	}

	.navtitle {
		margin-top: 10rpx;
		height: 50rpx;
		background-color: #FFFFFF;
	}

	.yello {
		width: 15rpx;
		height: 50rpx;
		background: #e89712;
		float: left;
		margin-left: 32rpx;
		margin-right: 10rpx;
	}

	.nav {
		font-size: 35rpx;
		font-weight: 300;
		line-height: 50rpx;
	}

	.navtitle .price {
		float: right;
		color: red;
	}
	.submit{
		width: 100%;
		margin-top: 20rpx;
		margin-bottom: 30rpx;
		background-color: #e09b12;
		color: #FFFFFF;
	}
</style>
